:root {
  --rad: .7rem;
  --font-fam: "Lato", sans-serif;
  --height: 3rem;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-evenly;
  padding-top: 30px;
  display: flex;
}

.info {
  justify-content: space-evenly;
  display: flex;
}

span {
  text-align: center;
  color: #000;
  font-weight: 300;
  display: block;
}

.header {
  height: 80px;
  background-color: #244b8f;
}

.search-form {
  width: 30rem;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  display: flex;
}

input, .searchBtn {
  height: var(--height);
  font-family: var(--font-fam);
  color: var(--color-dark);
  border: 0;
  font-size: 1.8rem;
}

input[type="text"] {
  width: 100%;
  border-radius: var(--rad);
  outline: 0;
  padding: 0 1.8rem;
}

.searchBtn {
  background: var(--color-brand);
  z-index: 10;
  color: #f0f8ff;
  margin-right: 50px;
  font-weight: bold;
}

input {
  width: calc(100% - var(--btn-width) );
}

